:root {
  --main: transparent;
  --main-tr: #75221f;
  /*  --main-tr: rgb(212, 48, 0); */
  --border: red;
}

.topbar {
  background: linear-gradient(
    to right,
    var(--main-tr) 10%,
    var(--main-tr),
    var(--main-tr) 90%
  );
}

.topbar::after {
  position: absolute;
  content: "";
  bottom: 0px;
  left: 0px;
  height: 1px;
  width: 100%;
  background: linear-gradient(
    90deg,
    transparent 77px,
    white 77px,
    white calc(100% - 77px),
    transparent calc(100% - 77px)
  );
}

.leftBlockTop {
  position: absolute;
  top: 10px;
  left: 0px;
  width: 60px;
  height: 36px;
  background-color: var(--main-tr);
  border-bottom: 2px solid white;
  z-index: -1;
}

.leftBlockTopShadow {
  position: absolute;
  bottom: -2px;
  width: 95%;
  height: 1px;
  box-shadow: 0px 0px 4px 1px rgb(255, 255, 255);
}

.leftBlockTop::after {
  position: absolute;
  content: "";
  bottom: -2px;
  right: -9px;
  width: 4px;
  height: 15px;
  background-color: rgb(255, 255, 255);
  transform: skew(-50deg);
  box-shadow: 0px 0px 4px 1px rgb(255, 255, 255);
  z-index: 90;
}

.leftBlockTop::before {
  position: absolute;
  content: "";
  bottom: -0px;
  right: -10px;
  width: 48px;
  z-index: 0;
  height: 13px;
  background-color: var(--main-tr);
  transform: skew(-50deg);
}

.rightBlockTop {
  position: absolute;
  top: 10px;
  right: 0px;
  width: 60px;
  height: 36px;
  background-color: var(--main-tr);
  border-bottom: 2px solid white;
  z-index: -1;
}

.rightBlockTopShadow {
  position: absolute;
  bottom: -2px;
  right: 0px;
  width: 95%;
  height: 1px;
  box-shadow: 0px 0px 4px 1px rgb(255, 255, 255);
}

.rightBlockTop::after {
  position: absolute;
  content: "";
  bottom: -2px;
  left: -9px;
  width: 4px;
  height: 15px;
  background-color: rgb(255, 255, 255);
  transform: skew(50deg);
  box-shadow: 0px 0px 4px 1px rgb(255, 255, 255);
  z-index: 9;
}

.rightBlockTop::before {
  position: absolute;
  content: "";
  bottom: 0px;
  left: -10px;
  width: 48px;
  z-index: 0;
  height: 13px;
  background-color: var(--main-tr);
  transform: skew(50deg);
}
